<template>
  <div>
    <el-header>
        <el-row :gutter="10">
        <el-col :span="4">
            <el-select v-model="leagueChoose" filterable clearable placeholder="联赛选择" style="width:100%;">
                <el-option
                v-for="item in leagueData"
                :key="item.league_id"
                :label="item.league_name"
                :value="item.league_id">
                </el-option>
            </el-select>
        </el-col>
        <el-col :span="4">
            <el-date-picker
                v-model="beginTime"
                type="datetime"
                placeholder="选择开始时间"
                value-format="yyyy-MM-dd HH:mm:ss"
                style="width:100%;">
            </el-date-picker>
        </el-col>
        <el-col :span="4">
            <el-date-picker
                v-model="endTime"
                type="datetime"
                placeholder="选择结束时间"
                value-format="yyyy-MM-dd HH:mm:ss"
                style="width:100%;">
            </el-date-picker>
        </el-col>
        <el-col :span="4">
            <el-select v-model="homeChoose" filterable clearable placeholder="主队选择" style="width:100%;">
                <el-option
                v-for="item in homeData"
                :key="item.home_id"
                :label="item.home_name"
                :value="item.home_id">
                </el-option>
            </el-select>
        </el-col>
        <el-col :span="4">
            <el-select v-model="awayChoose" filterable clearable placeholder="客队选择" style="width:100%;">
                <el-option
                v-for="item in awayData"
                :key="item.away_id"
                :label="item.away_name"
                :value="item.away_id">
                </el-option>
            </el-select>
        </el-col>
        <el-col :span="4">
            <el-button type="primary" plain style="width:100%;" @click="initData()">档案查询</el-button>
        </el-col>
        </el-row>
    </el-header>
    <el-table
      :data="tableData"
      border
      style="width:98%;margin:10px auto;">
      <el-table-column
        prop="league_name"
        label="赛事"
        align="center"
        width="300"
        >
      </el-table-column>
      <el-table-column
        prop="comm_time"
        label="比赛时间"
        align="center"
        width="200">
      </el-table-column>
      <el-table-column
        prop="home_name"
        align="center"
        label="主队">
      </el-table-column>
      <el-table-column
        prop="away_name"
        align="center"
        label="客队">
      </el-table-column>
      <el-table-column
        label="查看"
        align="center"
        width="200"
      >
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="openTo('/asia',scope.row)">亚</el-button>
          <el-button type="text" size="small" @click="openTo('/europe',scope.row)">欧</el-button>
          <el-button type="text" size="small" @click="openTo('/goal',scope.row)">大</el-button>
          <el-button type="text" size="small" @click="openTo('/scroll',scope.row)">滚</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'Main',
  data () {
    return {
      leagueData: [],
      leagueChoose: '',
      beginTime: '',
      endTime: '',
      homeData: [],
      homeChoose: '',
      awayData: [],
      awayChoose: '',
      tableData: []

    }
  },
  mounted () {
    this.initLeague()
    this.initHome()
    this.initAway()
  },
  methods: {
    initLeague () {
      this.$axios.get('/league/list.json')
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }
          this.leagueData = respData.result
        })
    },
    initHome () {
      this.$axios.get('/upcoming/home.json')
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }
          this.homeData = respData.result
        })
    },
    initAway () {
      this.$axios.get('/upcoming/away.json')
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }
          this.awayData = respData.result
        })
    },
    initData () {
      let data = new FormData()
      data.append('league_id', this.leagueChoose || '')
      data.append('begin_time', this.beginTime || '')
      data.append('end_time', this.endTime || '')
      data.append('home_id', this.homeChoose || '')
      data.append('away_id', this.awayChoose || '')

      this.$axios.post('/upcoming/history_query.json', data)
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }
          this.tableData = respData.result
        })
    },
    submitQuery () {
      console.dir(this.leagueChoose)
    },
    goTo (target, data) {
      clearInterval(this.intervalId)
      this.$router.push({
        path: target,
        query: {home_name: data.home_name, away_name: data.away_name, comm_id: data.comm_id}
      })
    },
    openTo (taget, data) {
      let routeData = this.$router.resolve({ path: taget, query: {home_name: data.home_name, away_name: data.away_name, comm_id: data.comm_id} })
      window.open(routeData.href, '_blank')
    }
  },
  destroyed () {
    clearInterval(this.intervalId)
  }
}
</script>

<style scoped>
</style>



